import React, { useEffect, useState } from 'react';
import { Button, Pagination } from 'antd';
import { PlusOutlined } from '@ant-design/icons';
import './MainPage.scss';
import AddCustomerModal from './AddCustomerModal/AddCustomerModal';
import Customer from '../../Customer/Customer';
import { RootState } from '../../../redux/Reducers/rootReducer';
import { useDispatch, useSelector } from 'react-redux';
import { loadCustomersByRequest } from '../../../redux/Actions/thunkCustomerActions';
import AppLoading from '../../AppLoading/AppLoading';

const MainPage: React.FC = () => {
  const [issLoading, setIsLoading] = useState(true);
  const customer = useSelector((state: RootState) => state.customer.customer);
  //const [customer, setIsCustomer] = useState<CustomerType[]>([]);
  const [isModalVisible, setIsModalVisible] = useState<boolean>(false);
  const dispatch = useDispatch();
  //console.log(customer);
  /*const customer: CustomerType[] = [];

  for (let i = 0; i < 12; ++i) {
    customer.push({
      _id: '32',
      firstName: 'Europe Street beat',
      lastName: '',
      secondName: '',
      email: 'tyt@email.la',
      phone: '01',
      address: '-',
    });
  }*/

  useEffect(() => {
    dispatch(loadCustomersByRequest());
    setIsLoading(false);
  }, [dispatch]);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const closeModal = () => {
    setIsModalVisible(false);
  };

  return (
    <div className="main-page">
      {issLoading ? (
        <AppLoading />
      ) : (
        <>
          <div className="main-page__header">
            <div className="title"></div>
          </div>
          <Button onClick={showModal} className="btn" icon={<PlusOutlined />} size="large">
             
          </Button>
          <div className="customers">
            <div className="customers__list">
              {customer.map((customer) => (
                <div key={customer.id} className="customers__item">
                  <Customer key={customer.id} customer={customer} />
                </div>
              ))}
            </div>
          </div>
          <Pagination responsive defaultCurrent={1} total={50} />
          <AddCustomerModal visible={isModalVisible} onCancel={closeModal} />
        </>
      )}
    </div>
  );
};

export default MainPage;
