import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/Reducers/rootReducer';
import { logoutCurrentUser } from '../../../redux/Actions/userActions';
import { Link } from 'react-router-dom';
import './Header.scss';
import logo from '../../../assets/images/logo.png';
import logoutImg from '../../../assets/images/logout.png';

const Header = () => {
  const dispatch = useDispatch();

  const isAuth = useSelector((state: RootState) => state.user.isAuth);
  const user = useSelector((state: RootState) => state.user.currentUser);

  const logout = () => {
    dispatch(logoutCurrentUser());
  };

  return (
    <div className="header">
      <div className="container">
        <div className="header__content">
          <div className="header__logo">
            <Link to="/">
              <img src={logo} alt="Logo" />
              <span>  </span>
            </Link>
          </div>
          {isAuth ? (
            <>
              <div className="user">
                <div className="user__caption">  :</div>
                <div className="user__name">{user.username}</div>
                <div className="user__pos">  </div>
              </div>
              <div onClick={logout} className="header__logout">
                <img src={logoutImg} alt="  " />
                <span> </span>
                <span></span>
              </div>
            </>
          ) : (
            <div className="header__auth">
              <button className="auth__button-registry auth__button">
                <Link to="/auth/signup" style={{ color: '#fff' }}>
                  
                </Link>
              </button>
              <button className="auth__button-login auth__button">
                <Link to="/auth" style={{ color: '#000' }}>
                  
                </Link>
              </button>
            </div>
          )}
        </div>
      </div>
    </div>
  );
};

export default Header;
