import axios from 'axios';
import { authHeader, url } from '../config/config';

class reportAPI {
  async getAllReports(id: string) {
    return await axios
      .get(`${url}/calculations/${id}`, { headers: authHeader() })
      .then((response) => {
        return response.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }
  async getReportById(id: string) {
    return await axios
      .get(`${url}/calculations/results/${id}`, { headers: authHeader() })
      .then((response) => {
        return response.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async createReportByCustomer(id: string | undefined, report: any) {
    return await axios
      .post(`${url}/calculations/create/${id}`, report, { headers: authHeader() })
      .then((res) => {
        return res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }
  async deleteReport(id: number) {
    return await axios
      .delete(`${url}/calculations/delete/${id}`, { headers: authHeader() })
      .then((res) => {
        return res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }
}

export default new reportAPI();
